.map-container {
    width: 100vw;
    height: 100vh;
    display: flex;
    overflow: hidden;
}
.left {
    width: 200px;
    height: 100%;
    display: flex;
    flex-direction: column;
}
.right {
    flex: 1;
    height: 100%;
    display: flex;
}

.left .list {
    flex: 1;
    padding-top: 20px;
    overflow-y: auto;
}

.left .uploader {
    padding: 20px;
}

.poi-item {
    padding: 0 10px;
}

.poi-item h2 {
    font-size: 20px;
    text-align: left;
    margin: 0;
    margin-bottom: 10px;
}

.poi-detail {
    margin: 0;
}

.poi-detail i {
    text-align: left;
    margin: 0;
}

.poi-detail span {
    text-align: left!important;
}

.poi-detail-item {
    padding: 10px;
    margin-bottom: 10px;
    border-radius: 5px;
    padding-right: 30px;
    cursor: pointer;
    position: relative;
}

.poi-detail-item-icon {
    position: absolute;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
}

.poi-detail-item--active {
    background-color: #f1f1f1;
}

.poi-detail-item h4 {
    margin: 0;
    text-align: left;
}

.poi-detail-item i {
    text-align: left;
    font-style: normal;
}